// Tab component
interface TabProps {
  children: React.ReactNode;
  active?: boolean;
}

const Tab: React.FC<TabProps> = ({ children, active = false }) => (
  <div className="flex flex-col items-start overflow-hidden cursor-pointer">
    <div className="flex items-center justify-center py-1 px-4 gap-2">
      <span className={`font-roboto text-body-sm font-medium ${
        active ? 'text-canary-blue-1' : 'text-canary-black-2'
      }`}>
        {children}
      </span>
    </div>
    <div className={`h-1 w-full ${active ? 'bg-canary-blue-1' : 'bg-transparent'}`} />
  </div>
);

// Button component
interface ButtonProps {
  children: React.ReactNode;
  variant?: 'primary' | 'icon';
  onClick?: () => void;
}

const Button: React.FC<ButtonProps> = ({ children, variant = 'primary', onClick }) => {
  if (variant === 'icon') {
    return (
      <button
        onClick={onClick}
        className="flex items-center justify-center p-2 rounded bg-transparent border-none cursor-pointer hover:bg-gray-50"
      >
        {children}
      </button>
    );
  }

  return (
    <button
      onClick={onClick}
      className="flex items-center justify-center gap-2 h-10 px-4 bg-canary-blue-1 text-white border-none rounded font-roboto text-body-sm font-medium cursor-pointer hover:opacity-90"
    >
      {children}
    </button>
  );
};

// Menu item component
interface MenuItemProps {
  name: string;
  entryPoint: string;
  onEdit?: () => void;
  onDelete?: () => void;
  isLast?: boolean;
}

const MenuItem: React.FC<MenuItemProps> = ({ name, entryPoint, onEdit, onDelete, isLast = false }) => (
  <div className={`flex items-center justify-between py-4 pl-4 pr-6 bg-white ${
    isLast ? '' : 'border-b border-neutral-200'
  }`}>
    <div className="flex items-center gap-4 flex-1 min-w-0">
      <div className="w-48 lg:w-48 md:w-40 sm:w-32 font-roboto text-body-sm font-medium text-canary-black-1 truncate">
        {name}
      </div>
    </div>
    
    <div className="w-48 lg:w-48 md:w-40 sm:w-32 hidden sm:block font-roboto text-body-sm font-normal text-canary-black-1 truncate">
      {entryPoint}
    </div>
    
    <div className="flex items-center gap-2 shrink-0">
      <Button variant="icon" onClick={onEdit}>
        <EditIcon />
      </Button>
      <Button variant="icon" onClick={onDelete}>
        <DeleteIcon />
      </Button>
    </div>
  </div>
);

// Navigation Item component
interface NavItemProps {
  children: React.ReactNode;
  active?: boolean;
}

const NavItem: React.FC<NavItemProps> = ({ children, active = false }) => (
  <div className={`h-10 w-full relative flex items-center px-4 cursor-pointer rounded ${
    active ? 'bg-white' : 'hover:bg-white/10'
  }`}>
    <div className="mr-3">
      <FoodIcon />
    </div>
    <span className={`font-roboto text-body-sm font-normal ${
      active ? 'text-canary-black-1' : 'text-white'
    }`}>
      {children}
    </span>
  </div>
);

